.b-about-review {
	padding-top: 20px;
	padding-bottom: 20px;
	position: relative;
	text-align: center;
	&:before {
		content: '';
		display: block;
		background: transparent url(data:image/svg+xml;base64,) center no-repeat;
		-webkit-background-size: contain;
		background-size: contain;
		width: 54px;
		height: 40px;

		margin: 0 auto 18px;
		@include rotate(180deg); }

	p {
		font-size: 24px;
		font-weight: 900;
		line-height: 1.13; }


	@media screen and (min-width: $lg) {
		padding-top: 40px;
		padding-bottom: 40px;
		&:before {
			width: 78px;
			height: 50px; }

		p {
			font-size: 36px; } } }


