.tns-outer {
  padding: 0 !important;
}

.tns-outer [hidden] {
  display: none !important;
}

.tns-outer [aria-controls], .tns-outer [data-action] {
  cursor: pointer;
}

.tns-slider {
  -webkit-transition: all 0s;
  -moz-transition: all 0s;
  transition: all 0s;
}

.tns-slider > .tns-item {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.tns-horizontal.tns-subpixel {
  white-space: nowrap;
}

.tns-horizontal.tns-subpixel > .tns-item {
  display: inline-block;
  vertical-align: top;
  white-space: normal;
}

.tns-horizontal.tns-no-subpixel:after {
  content: '';
  display: table;
  clear: both;
}

.tns-horizontal.tns-no-subpixel > .tns-item {
  float: left;
}

.tns-horizontal.tns-carousel.tns-no-subpixel > .tns-item {
  margin-right: -100%;
}

.tns-no-calc {
  position: relative;
  left: 0;
}

.tns-gallery {
  position: relative;
  left: 0;
  min-height: 1px;
}

.tns-gallery .tns-item {
  position: absolute;
  left: -100%;
  -webkit-transition: transform 0s, opacity 0s;
  -moz-transition: transform 0s, opacity 0s;
  transition: transform 0s, opacity 0s;
}

.tns-gallery .tns-item .tns-slide-active {
  position: relative;
  left: auto !important;
}

.tns-gallery .tns-item .tns-moving {
  -webkit-transition: all 0.25s;
  -moz-transition: all 0.25s;
  transition: all 0.25s;
}

.tns-autowidth {
  display: inline-block;
}

.tns-lazy-img {
  -webkit-transition: opacity 0.6s;
  -moz-transition: opacity 0.6s;
  transition: opacity 0.6s;
  opacity: 0.6;
}

.tns-lazy-img.loaded {
  opacity: 1;
}

.tns-ah {
  -webkit-transition: height 0s;
  -moz-transition: height 0s;
  transition: height 0s;
}

.tns-ovh {
  overflow: hidden;
}

.tns-visually-hidden {
  position: absolute;
  left: -10000em;
}

.tns-transparent {
  opacity: 0;
  visibility: hidden;
}

.tns-fadeIn {
  opacity: 1;
  filter: alpha(opacity=100);
  z-index: 0;
}

.tns-normal, .tns-fadeOut {
  opacity: 0;
  filter: alpha(opacity=0);
  z-index: -1;
}

.tns-t-subp2 {
  margin: 0 auto;
  width: 310px;
  position: relative;
  height: 10px;
  overflow: hidden;
}

.tns-t-ct {
  width: 2333.3333333%;
  width: -webkit-calc(100% * 70 / 3);
  width: -moz-calc(100% * 70 / 3);
  width: calc(100% * 70 / 3);
  position: absolute;
  right: 0;
}

.tns-t-ct:after {
  content: '';
  display: table;
  clear: both;
}

.tns-t-ct > div {
  width: 1.4285714%;
  width: -webkit-calc(100% / 70);
  width: -moz-calc(100% / 70);
  width: calc(100% / 70);
  height: 10px;
  float: left;
}

@keyframes bounceInDown {
  0% {
    transform: translate3d(0, -10px, 0);
  }
  60% {
    transform: translate3d(0, 0px, 0);
  }
  75% {
    transform: translate3d(0, 0px, 0);
  }
  90% {
    transform: translate3d(0, -2px, 0);
  }
  to {
    transform: translate3d(0, -2px, 0);
  }
}

@keyframes bounceUp {
  0% {
    transform: translate3d(0, 0px, 0);
  }
  60% {
    transform: translate3d(0, -12px, 0);
  }
  75% {
    transform: translate3d(0, -12px, 0);
  }
  90% {
    transform: translate3d(0, -10px, 0);
  }
  to {
    transform: translate3d(0, -10px, 0);
  }
}

@keyframes buttonAnimate {
  0% {
    transform: rotate(-8deg) scale(1.05);
  }
  50% {
    transform: rotate(0deg) scale(0.97);
  }
  60% {
    transform: rotate(0deg) scale(0.99);
  }
  to {
    transform: rotate(0deg) scale(1);
  }
}

@keyframes buttonAnimateHover {
  0% {
    transform: rotate(0deg) scale(1);
  }
  50% {
    transform: rotate(-8deg) scale(1.07);
  }
  60% {
    transform: rotate(-8deg) scale(1.05);
  }
  to {
    transform: rotate(-8deg) scale(1.05);
  }
}

@keyframes headerAnimation {
  0% {
    opacity: 0;
    transform: translate(0, -100%);
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@keyframes buttonCorner {
  0% {
    opacity: 0;
    transform: translate(0, -100%);
  }
  80% {
    opacity: 1;
    transform: translate(0, 2px);
  }
  100% {
    transform: translate(0, 0%);
  }
}

@keyframes welcomeAnimation {
  0% {
    opacity: 0;
    transform: translate(0, 20%);
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@keyframes card-open {
  0% {
    height: 0px;
    width: 0px;
    top: 20px;
    right: 20px;
    border-radius: 50%;
  }
  100% {
    height: 1000px;
    width: 1000px;
    top: -480px;
    right: -480px;
    border-radius: 50%;
  }
}

@keyframes card-close {
  0% {
    height: 1000px;
    width: 1000px;
    top: -480px;
    right: -480px;
    border-radius: 50%;
  }
  100% {
    height: 0px;
    width: 0px;
    top: 20px;
    right: 20px;
    border-radius: 50%;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translatey(20px);
  }
  50% {
    opacity: .5;
  }
  100% {
    opacity: 1;
    transform: translatey(0px);
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
    transform: translatey(0px);
  }
  50% {
    opacity: .5;
  }
  100% {
    opacity: 0;
    transform: translatey(20px);
  }
}

@keyframes fadeOutSmall {
  0% {
    opacity: 1;
    transform: translatey(0%);
  }
  50% {
    opacity: .5;
  }
  75% {
    opacity: .5;
  }
  100% {
    opacity: 0;
    transform: translatey(200%);
  }
}

@keyframes fadeInSmall {
  0% {
    opacity: 0;
    transform: translatey(200%);
  }
  50% {
    opacity: .5;
  }
  75% {
    opacity: .5;
  }
  100% {
    opacity: 1;
    transform: translatey(0%);
  }
}

@keyframes createButtonMobile {
  0% {
    opacity: 0;
    transform: translatey(20px);
  }
  50% {
    opacity: .5;
  }
  100% {
    opacity: 1;
    transform: translatey(0px);
  }
}

@keyframes spin {
  to {
    transform: rotate(0.5turn);
  }
}

@keyframes bg {
  50% {
    background: #e6eaee;
  }
}

body,
html {
  height: 100%;
  margin: 0;
}

body {
  font: 16px/1.4 "Prompt", Helvetica, Arial, sans-serif;
  color: #000000;
  overflow: hidden;
}

body * {
  box-sizing: border-box;
  outline: none;
}

body *:hover, body *:focus {
  outline: none;
}

body.js-mobile-menu {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #000000;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

a svg {
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

a:hover, a:focus {
  color: #40ba87;
}

a:hover svg, a:focus svg {
  fill: #40ba87;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
}

.h1 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 25px;
}

@media screen and (min-width: 1025px) {
  .h1 {
    font-size: 36px;
    margin-bottom: 50px;
  }
}

p {
  font-size: 16px;
  margin-top: 0;
  margin-bottom: 15px;
}

button, input, textarea, .e-input {
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

input, textarea, .e-input {
  display: block;
  border-radius: 6px;
  border: 1px solid #e6eaee;
  width: 100%;
  font-size: 16px;
  padding: 8px 12px 8px;
  position: relative;
  z-index: 1;
  background-color: transparent;
}

input:focus, input:required:valid, textarea:focus, textarea:required:valid, .e-input:focus, .e-input:required:valid {
  border-color: #40ba87;
}

@media screen and (min-width: 1025px) {
  input, textarea, .e-input {
    padding: 10px 12px 10px;
  }
}

.e-input-placeholder {
  position: absolute;
  top: 50%;
  left: 11px;
  font-size: 11px;
  line-height: 1;
  color: #262a2c;
  opacity: .55;
  background-color: #ffffff;
  padding-left: 2px;
  padding-right: 2px;
  -webkit-transform: translate(0, -50%);
  -moz-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.e-input:focus ~ .e-input-placeholder {
  opacity: 1;
  color: #40ba87;
  top: 0;
  z-index: 1;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  -moz-appearance: textfield;
  margin: 0;
  padding: 0;
  width: 0;
  display: none;
}

input[type="time"] {
  font-size: 13px;
  padding-right: 0;
}

label {
  cursor: pointer;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
  box-shadow: none;
  cursor: pointer;
}

video {
  max-width: 100%;
}

table {
  width: 100%;
}

::selection {
  background-color: #f7da5f;
  color: #fff;
  text-shadow: none;
