}

.c-header {
  position: relative;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  padding: 8px;
  background-color: #ffffff;
  z-index: 5;
}

.c-header:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  display: block;
  width: 100%;
  height: 4px;
  background-image: linear-gradient(to top, rgba(230, 234, 238, 0) 0%, #e6eaee 100%);
  z-index: -2;
}

.c-header--style-2 {
