}

.c-header--style-2:after {
  display: none;
}

.c-header__logo {
  position: relative;
  z-index: 15;
  padding-right: 12px;
}

.c-header__navigation {
  padding-right: 12px;
  padding-left: 12px;
}

.c-header__right {
  position: relative;
  display: flex;
  align-items: center;
  margin-left: auto;
  padding-left: 12px;
}

.c-header__button-corner {
  display: none;
}

.c-header__burger {
  width: 48px;
}

.c-header__create-button-mobile {
  margin: 100px -16px 0 -16px;
  text-align: left;
}

.c-content {
  padding: 50px 0 50px;
}

.c-footer {
  padding: 38px 0;
  flex: 0 0 auto;
  border-top: 1px solid #e6eaee;
  margin-left: 20px;
  margin-right: 20px;
}

.c-footer__caption {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 8px;
}

.c-footer__copyright {
  border-top: 1px solid #e6eaee;
  padding-top: 38px;
  margin-top: 38px;
}

.main-bold {
  font-weight: 600;
}

.c-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.c-justify-center {
  justify-content: center;
}

@media screen and (max-width: 1024px) {
  .c-page .o-editor__map {
    animation: fadeInSmall .4s;
    animation-fill-mode: forwards;
  }
  .c-page .o-editor__sidebar {
    animation: fadeInSmall .4s;
    animation-fill-mode: forwards;
  }
  .c-page.js-mobile-menu .o-editor__map {
    animation: fadeOutSmall .3s;
    animation-fill-mode: forwards;
  }
  .c-page.js-mobile-menu .o-editor__sidebar {
    animation: fadeOutSmall .3s;
    animation-fill-mode: forwards;
  }
  .c-header__create-button-mobile {
    opacity: 0;
    transform: translatey(20px);
  }
  .c-header__navigation {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    left: 0;
    right: 0;
    top: 0;
    overflow-y: auto;
    padding: 150px 48px 0;
    opacity: 0;
    visibility: hidden;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -ms-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
  }
  .c-header__navigation .b-navigation__item {
    animation: fadeOut .2s;
    animation-fill-mode: forwards;
  }
  .c-header__navigation.js-active {
    bottom: 0;
    opacity: 1;
    visibility: visible;
  }
  .c-header__navigation.js-active .b-navigation__item {
    animation: fadeIn 0.4s;
    animation-fill-mode: forwards;
  }
  .c-header__navigation.js-active .b-navigation__item:nth-child(1) {
    animation-delay: 0.3s;
  }
  .c-header__navigation.js-active .b-navigation__item:nth-child(2) {
    animation-delay: 0.4s;
  }
  .c-header__navigation.js-active .b-navigation__item:nth-child(3) {
    animation-delay: 0.5s;
  }
  .c-header__navigation.js-active .b-navigation__item:nth-child(4) {
    animation-delay: 0.6s;
  }
  .c-header__navigation.js-active .c-header__create-button-mobile {
    animation: createButtonMobile .7s .4s;
    animation-fill-mode: forwards;
  }
}

@media screen and (min-width: 1025px) {
  .c-container {
    margin-left: 100px;
    margin-right: 100px;
  }
  .c-header__logo {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-name: headerAnimation;
  }
  .c-header__navigation {
