    font-size: 36px;
  }
}

.b-author {
  text-align: center;
}

.b-author__photo {
  width: 36px;
  height: 36px;
  overflow: hidden;
  margin: 0 auto 5px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
}

.b-author__name {
  display: block;
  line-height: 1;
}

.b-author__position {
  display: block;
  opacity: .55;
  color: #262a2c;
  line-height: 1;
}

.b-inspiration-list {
  margin: 0 -20px;
  display: flex;
  flex-wrap: wrap;
}

.b-inspiration-list__item {
  display: inline-block;
  padding: 0 20px 20px;
}

@media screen and (max-width: 1024px) {
  .b-inspiration-list {
    justify-content: center;
  }
}
