.e-burger {
	display: flex;
	align-items: center;
	justify-content: center;

	position: absolute;

	top: -25px;
	right: -20px;

	width: 72px;
	height: 72px;
	//background-color: $color-accent
	@include border-radius(38px);
	@include transition(all, .7s, ease);
	transition-delay: .3s;
	&:before {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		height: 100%;

		background-color: $color-accent;
		z-index: -1;
		@include border-radius(50%);
		@include transition(all, .7s, cubic-bezier(0.405, 0.00, 0.165, 1.000)); }
	&:after {
		content: '';
		z-index: -1;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 0;
		height: 0;
		background-color: #ffffff;
		margin: auto;
		@include scale(0);
		@include border-radius(50%);
		@include transition(all, .7s, ease); }

	&__line {
		position: relative;
		top: 1px;
		right: 4px;
		height: 2px;
		width: 24px;
		background-color: $color-dark;
		@include transition(all, .7s, ease);
		&:before,
		&:after {
			content: '';
			position: absolute;
			display: block;
			height: 2px;
			width: 12px;
			background-color: $color-dark;
			@include transition(all, .3s, ease); }

		&:before {
			top: -8px;
			right: 0; }
		&:after {
			bottom: -8px;
			left: 0; } }
	&.js-active {
		//background-color: #ffffff
		&:after {
			@include scale(1);
			width: 74px;
			height: 74px;
			transition-delay: .3s; }
		&:before {
			width: 100vh;
			height: 100vh;
			transition-duration: .3s;
			@include scale(5); }


		.e-burger__line {
			@include rotate(-45deg);
			transition-delay: .3s;
			&:before,
			&:after {
				transition-delay: .3s;
				width: 8px; }

			&:before {
				right: 8px; }
			&:after {
				left: 8px; } } } }
