.b-editor-map {
	position: relative;
	width: 240px;
	//width: 66%
	margin: 0 auto;
	box-shadow: 0 12px 30px rgba(0, 0, 0, 0.1), 0 1px 6px rgba(0, 0, 0, 0.15);
	background-color: #ffffff;

	display: flex;
	flex-direction: column;
	height: 100%;
	max-width: 100%;

	&--style-2 {
		padding: 5px; }
	&--style-4 {
		&:before {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			width: 100%;
			height: 120px;
			background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
			z-index: 1; } }
	&__map {
		height: 240px;

		min-height: 100%;

		&--style-2 {
			height: 280px; } }
	&__desc {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 8px 11px 11px;
		min-height: 120px;

		flex: 1 1 30%;
 }		//height: 35%
	&__desc-style-2 {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;

		&:before {
			content: '';
			position: absolute;
			top: 50%;
			left: 0;
			right: 0;
			background-color: #ffffff;
			width: 100%;
			height: 8px;
			@include translate(0, -50%); } }
	&__desc-style-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		text-align: center; }
	&__desc-style-4 {
		position: absolute;
		bottom: 15px;
		left: 0;
		right: 0;
		text-align: center;
		z-index: 2; }
	&__coordinates {
		text-transform: uppercase;
		font-size: 12px;
		font-weight: 300; }

	&__city {
		font-weight: 700;
		font-size: 20px;
		text-transform: uppercase;
		line-height: .9; }
	&__country {
		display: block;
		font-weight: 200; }

	&__hint {
		font-size: 11px;
		opacity: .55;
		margin: 9px auto 0;
		text-align: center; }
	&__map-wrap {
		position: relative;

		//max-height: 65%
		flex: 1 1 70%;
		overflow: hidden; }
	&__fullscreen {
		z-index: 3;
		position: absolute;
		top: 8px;
		right: 8px;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 24px;
		height: 24px;
		box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
		border-radius: 6px;
		background-color: #ffffff; }
	&__fullscreen-icon {
		font-size: 14px;
		height: 14px;
 }		//opacity: .4
	&__pencil {
		display: none;
		position: absolute;
		width: 16px;
		height: 206px;
		top: 50%;
		left: calc(100% + 18px);

		background: transparent url("../assets/img/pencil.png") center no-repeat;
		background-size: contain;
		@include translate(0, -50%); }
	&.landscape {
		width: 100%;
		.b-editor-map {
			&__map {
				height: 200px; }
			&__desc {
				background-color: #ffffff;
				position: absolute;
				bottom: 0;
				left: 0;
				padding: 5px;
				min-height: 51px; }
			&__city {
				font-size: 10px; }
			&__coordinates {
				font-size: 5px; }
			&__location {
				font-size: 5px; }
			&__desc-style-2 {
				bottom: 0;
				right: auto; }
			&__desc-style-3 {
				bottom: 0;
				right: auto; }
			&__desc-style-4 {
				bottom: 15px; }
			&__pencil {
				top: calc(100% + 32px);
				left: 0;
				right: 0;
				margin: 0 auto;
				transform: rotate(-90deg) translate(80px, 0); } }
		.b-editor-map-desc-style-2 {
			width: 117px;
			padding: 5px;
			border-radius: 0;
			&:before,
			&:after {
				display: none; }
			&__city {
				font-size: 10px; }
			&__location {
				font-size: 5px; } }

		.b-editor-map-desc-style-3 {
			width: 117px;
			padding: 5px;
			&__city {
				font-size: 10px; }
			&__location,
			&__coordinates {
				font-size: 8px; } } }
	@media screen and (min-width: $sm) {
		width: 480px;
		&--style-4 {
			&:before {
				height: 241px; } }
		&.landscape {
			width: 480px;
			&.b-editor-map--style-4 {
				&:before {
					height: 120px; } }
			.b-editor-map {
				&__map {
					height: 240px; }
				&__coordinates {
					font-size: 12px; }
				&__city {
					font-size: 20px; }
				&__desc {
					min-height: 80px;
					min-width: 170px; } }

			.b-editor-map-desc-style-2 {
				width: 180px;
				&__city {
					font-size: 18px; }
				&__location {
					font-size: 12px; } }
			.b-editor-map-desc-style-3 {

				//width: 180px
				&__city {
					font-size: 20px; }
				&__location {
					font-size: 10px; } } }
		&__map {
			height: 350px;
			&--style-2 {
				height: 480px; } }
		&__desc {
			//min-height: 230px
			min-height: 117px;
			//padding: 16px 20px 20px
			padding: 9px 12px 9px; }

		&__coordinates {
			font-size: 12px; }
		&__city {
			font-size: 20px; }
		&__desc-style-2 {
			bottom: 100px; }
		&__desc-style-3 {
			bottom: 31px; }
		&__desc-style-4 {
			bottom: 30px; }
		&__fullscreen {
			display: none; } }
	@media screen and (min-width: 767px) {
		&.landscape {
			width: 480px;
			&.b-editor-map--style-4 {
				&:before {
					height: 240px; } }
			.b-editor-map {
				&__map {
					height: 300px; } }
			.b-editor-map-desc-style-2 {
				width: 280px;
				&__city {
 }					//font-size: 24px
				&__location {
 } }					//font-size: 12px
			.b-editor-map-desc-style-3 {
				//width: 280px
				&__city {
					font-size: 18px; }
				&__location {
					font-size: 12px; } } }
		&__map {
			height: 480px;
			&--style-2 {
				height: 720px; } } }
	@media screen and (min-width: $lg) {
		width: 480px;
		&.landscape {
			width: 720px;
			max-width: 80%;
			.b-editor-map {
				&__map {
					height: 480px; }
				&__desc {
					padding: 12px;
					min-height: 120px;
					min-width: 280px; }
				&__coordinates {
 }					//font-size: 12px
				&__city {
 }					//font-size: 24px

				&__desc-style-3 {
					bottom: 35px;
					right: 0; } }
			.b-editor-map-desc-style-3 {
				padding: 12px 10px;
				&__city {
 } } }					//font-size: 32px


		&__desc {
			min-height: 120px; }
		&__hint {
			display: none; }

		&__pencil {
			display: block; }
		&__map-wrap {
			overflow: visible; } }

	@media only screen and (max-device-width: 778px) and (orientation: landscape) {
		width: 100%;
		&__map {
			height: 300px; }
		&__desc {
			position: absolute;
			bottom: 0;
			left: 0;
			background-color: #fff;
			padding: 5px;
			min-height: 51px;
			min-width: 117px; }
		&__city {
			font-size: 10px; }
		&__coordinates {
			font-size: 5px; }
		&__fullscreen {
			display: flex; }
		&__desc-style-2 {
			bottom: 0;
			right: auto;
			&:before {
				display: none; } }
		&__desc-style-3 {
			bottom: 0;
			right: auto; }
		&__desc-style-4 {
			bottom: 15px; } }
	@media only screen and (max-device-height: 400px) and (orientation: landscape) {

		&__map {
			height: 200px; } } }
